/*===== Separaciones =====*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 2.5rem;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

header {
  background-color: #141516;
}

.navegacion {
  display: grid;
  align-items: center;
  justify-content: center;
  justify-items: center;
}
.navegacion ul, .navegacion li {
  padding: 0;
  list-style: none;
  text-decoration: none;
  text-align: center;
}
.navegacion a, .navegacion h2 {
  color: white;
}
.navegacion a {
  font-size: 2rem;
}
.navegacion a:hover {
  color: #DF6160;
}
.navegacion h2 {
  margin: 0;
  user-select: none;
}
.navegacion .navegacion-logo {
  grid-row: 1/2;
}
@media (min-width: 769px) {
  .navegacion .navegacion-logo {
    grid-row: unset;
  }
}
@media (min-width: 769px) {
  .navegacion {
    grid-template-columns: repeat(2, 1fr);
  }
  .navegacion .navegacion-links {
    display: flex;
    gap: 2rem;
  }
}

.banner {
  width: 100%;
  height: 600px;
  animation: banner 10s infinite linear alternate;
  background-size: 100% 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

@keyframes banner {
  0%, 20% {
    background-image: url("../img/pexels-tima-miroshnichenko-5846275.webp");
  }
  25%, 45% {
    background-image: url("../img/pexels-tima-miroshnichenko-5846249.webp");
  }
  50%, 70% {
    background-image: url("../img/pexels-tima-miroshnichenko-5846277.webp");
  }
  75%, 100% {
    background-image: url("../img/pexels-tima-miroshnichenko-5845952.webp");
  }
}
.capa {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
}

.info {
  z-index: 1;
  width: 100%;
  max-width: 550px;
  text-align: center;
}

.info h1 {
  color: #fff;
  font-size: 45px;
  margin-bottom: 20px;
  letter-spacing: 1px;
}

.info p {
  color: #c7c7c7;
  line-height: 1.7;
  margin-bottom: 30px;
}

.info a {
  color: #fff;
  text-decoration: none;
  background: #3088e0;
  padding: 16px 37px;
  display: inline-block;
  border-radius: 50px;
}