@use '../base/variables' as v;
@use '../base/mixins' as m;


.banner{
  width: 100%;
  height: 600px;
  animation: banner 10s infinite linear alternate;
  background-size: 100% 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
@keyframes banner{
  0%, 20%{
    background-image: url("../img/pexels-tima-miroshnichenko-5846275.webp");
  }
  25%, 45%{
    background-image: url("../img/pexels-tima-miroshnichenko-5846249.webp");
  }
  50%, 70%{
    background-image: url("../img/pexels-tima-miroshnichenko-5846277.webp");
  }
  75%, 100%{
    background-image: url("../img/pexels-tima-miroshnichenko-5845952.webp");
  }
}
.capa{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0,0,0,0.6);
}
.info{
  z-index: 1;
  width: 100%;
  max-width: 550px;
  text-align: center;
}
.info h1{
  color: #fff;
  font-size: 45px;
  margin-bottom: 20px;
  letter-spacing: 1px;
}
.info p{
  color: #c7c7c7;
  line-height: 1.7;
  margin-bottom: 30px;
}
.info a{
  color: #fff;
  text-decoration: none;
  background: #3088e0;
  padding: 16px 37px;
  display: inline-block;
  border-radius: 50px;
}